import firebase from 'firebase/compat/app';

import { AuthFetch, AuthSuccess, AuthOut } from 'types/actions';
import { writeUserLastActive } from 'data/user';

export const login = () => {
  if (typeof firebase.auth === 'function') {
    const provider = new firebase.auth.GoogleAuthProvider();
    firebase.auth().useDeviceLanguage();
    void firebase.auth().signInWithRedirect(provider);
  }
};

export const handleAuthOut = (userID: string): void => {
  writeUserLastActive(userID);
  if (typeof firebase.auth === 'function') {
    void firebase.auth().signOut();
  }
};

export const trackAuthChange = ({
  fetching,
  success,
  out,
}: {
  fetching: () => AuthFetch;
  success: (userData: AuthSuccess['payload']) => AuthSuccess;
  out: () => AuthOut;
}): void => {
  fetching();
  const withEmail = firebase.auth().isSignInWithEmailLink(window.location.href);
  const callSuccess = (user: firebase.User) =>
    success({
      id: user.uid,
      name: user.displayName || '',
      email: user.email || '',
      photoURL: user.photoURL || '',
    });
  if (withEmail) {
    let email = window.localStorage.getItem('emailForSignIn');
    if (email) {
      firebase
        .auth()
        .signInWithEmailLink(email, window.location.href)
        .then((result) => {
          window.localStorage.removeItem('emailForSignIn');
          if (result.user && result.user.email) {
            callSuccess(result.user);
          }
        });
    }
    return;
  }
  firebase.auth().onAuthStateChanged((user) => {
    if (user && user.email) {
      callSuccess(user);
    } else {
      out();
    }
  });
};
